'use client';

import React, { useState } from 'react';
import { Event, Prediction } from '@/types';
import { ChevronDown, ChevronUp, Lock, CheckCircle2, AlertTriangle, Shield, Zap } from 'lucide-react';
import { cn } from '@/lib/utils';
import PredictionTable from './ui/PredictionTable';
import { Badge } from './ui/Badge';

interface SiteAccordionProps {
  events: Event[];
  isSubscribed: boolean;
}

export default function SiteAccordion({ events, isSubscribed }: SiteAccordionProps) {
  // Track which accordion item is open
  const [openEventId, setOpenEventId] = useState<number | null>(events[0]?.id || null);

  return (
    <div className="space-y-4">
      {events.map((event) => (
        <AccordionItem 
          key={event.id} 
          event={event} 
          isOpen={openEventId === event.id} 
          onToggle={() => setOpenEventId(openEventId === event.id ? null : event.id)}
          isSubscribed={isSubscribed}
        />
      ))}
    </div>
  );
}

// --- SUB-COMPONENT: The Individual Item ---
function AccordionItem({ event, isOpen, onToggle, isSubscribed }: { 
  event: Event, isOpen: boolean, onToggle: () => void, isSubscribed: boolean 
}) {
  const [checklistConfirmed, setChecklistConfirmed] = useState(false);
  const [activeTab, setActiveTab] = useState<'strat_a' | 'strat_b'>('strat_a');

  return (
    <div className="border border-gray-800 rounded-xl bg-gray-900/30 overflow-hidden transition-all">
      
      {/* HEADER (Clickable) */}
      <button 
        onClick={onToggle}
        className="w-full flex items-center justify-between p-4 hover:bg-white/5 transition-colors"
      >
        <div className="flex items-center space-x-4">
          <div className={cn("p-2 rounded-lg", isOpen ? "bg-cyan-500/20 text-cyan-400" : "bg-gray-800 text-gray-400")}>
            {event.platform === 'SportPesa' ? 'âš½' : 'ðŸŽ¯'}
          </div>
          <div className="text-left">
            <h3 className="text-lg font-bold text-white">{event.platform}</h3>
            <p className="text-xs text-gray-400">{event.event_name} â€¢ Week {event.week_number}</p>
          </div>
        </div>
        <div className="flex items-center space-x-3">
          {!isSubscribed && <Badge variant="warning">LOCKED</Badge>}
          {isOpen ? <ChevronUp className="w-5 h-5 text-gray-500" /> : <ChevronDown className="w-5 h-5 text-gray-500" />}
        </div>
      </button>

      {/* CONTENT BODY */}
      {isOpen && (
        <div className="border-t border-gray-800 p-4 bg-black/20">
          
          {/* STATE 1: CHECKLIST (If subscribed but not confirmed) */}
          {isSubscribed && !checklistConfirmed ? (
            <div className="py-8 text-center space-y-4 max-w-md mx-auto">
              <div className="bg-yellow-900/20 p-4 rounded-full inline-flex mb-2">
                <AlertTriangle className="w-8 h-8 text-yellow-500" />
              </div>
              <h4 className="text-xl font-bold text-white">Adherence Check</h4>
              <p className="text-gray-400 text-sm">
                To prevent misuse of the Hunter Protocol, you must confirm you understand the variance risks.
              </p>
              
              <div className="text-left bg-gray-900 p-4 rounded-lg space-y-3 my-4 border border-gray-800">
                <label className="flex items-center space-x-3 cursor-pointer">
                  <input type="checkbox" className="form-checkbox h-4 w-4 text-cyan-500 bg-gray-800 border-gray-600 rounded" />
                  <span className="text-sm text-gray-300">I understand Strategy A (Shield) is for safety.</span>
                </label>
                <label className="flex items-center space-x-3 cursor-pointer">
                  <input type="checkbox" className="form-checkbox h-4 w-4 text-cyan-500 bg-gray-800 border-gray-600 rounded" />
                  <span className="text-sm text-gray-300">I accept that Strategy B (Sword) has high variance.</span>
                </label>
              </div>

              <button 
                onClick={() => setChecklistConfirmed(true)}
                className="w-full py-3 bg-cyan-600 hover:bg-cyan-500 text-black font-bold rounded-lg transition-all flex items-center justify-center"
              >
                <CheckCircle2 className="w-4 h-4 mr-2" /> REVEAL INTELLIGENCE
              </button>
            </div>
          ) : (
            /* STATE 2: THE REVEAL (Tabs + Table) */
            <div className="space-y-4 animate-in fade-in duration-500">
              
              {/* TABS */}
              <div className="flex p-1 bg-gray-800/50 rounded-lg">
                <button
                  onClick={() => setActiveTab('strat_a')}
                  className={cn(
                    "flex-1 flex items-center justify-center py-2 text-sm font-bold rounded-md transition-all",
                    activeTab === 'strat_a' ? "bg-cyan-900/50 text-cyan-400 shadow-sm" : "text-gray-500 hover:text-gray-300"
                  )}
                >
                  <Shield className="w-4 h-4 mr-2" /> STRATEGY A (SHIELD)
                </button>
                <button
                  onClick={() => setActiveTab('strat_b')}
                  className={cn(
                    "flex-1 flex items-center justify-center py-2 text-sm font-bold rounded-md transition-all",
                    activeTab === 'strat_b' ? "bg-purple-900/50 text-purple-400 shadow-sm" : "text-gray-500 hover:text-gray-300"
                  )}
                >
                  <Zap className="w-4 h-4 mr-2" /> STRATEGY B (SWORD)
                </button>
              </div>

              {/* DATA TABLE */}
              <PredictionTable 
                predictions={event.predictions || []} 
                isSubscribed={isSubscribed}
                strategyMode={activeTab}
              />
            </div>
          )}
        </div>
      )}
    </div>
  );
}